import React, { Fragment } from 'react';
import Helmet from 'react-helmet';
import { navigateTo } from 'gatsby-link';
import PropTypes from 'prop-types';
import axios from 'axios';
import Frame from '../../components/Global/Frame';
import { connect } from 'react-redux';
import withRoot from '../../withRoot';
import { createStructuredSelector } from 'reselect';
import { withReduxSaga } from '../../../store';
import { loadPesan } from '../../../actions/gatolochoAction';
import { selectSatu } from '../../../selectors/gatolochoSelector';

import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Button from 'material-ui/Button';
import TextField from 'material-ui/TextField';
import List, { ListItem, ListItemIcon, ListItemText } from 'material-ui/List';

import AccountBoxIcon from 'material-ui-icons/AccountBox';
import PhoneIcon from 'material-ui-icons/Phone';
import EmailIcon from 'material-ui-icons/Email';

const styles = theme => ({
  root: {
    maxWidth: 500,
    margin: '0 auto',
    marginTop: 25,
  },
  action: {
    marginTop: 10,
  },
});

const getPass = () => {
  const check = typeof window !== 'undefined';
  return check && window.localStorage.getItem('pass');
};

class ContactMe extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      data: [],
      order: 'dibaca',
    };
  }

  componentDidMount = () => {
    if (getPass() === 'kodokngorek' && this.props.satuData === null) {
      this.props.dispatch(loadPesan());
    }
  }

  render() {
    const { classes, satuData } = this.props;
    const { name, data } = this.state;
    console.log('snp', satuData);
    return (
      <Fragment>
        <Helmet defaultTitle="contact me">
          <title>contact me</title>
          <html lang="en" />
        </Helmet>
        <Frame>
          <div className={classes.root}>
            <List style={{ background: '#FFF', borderRadius: 8 }} component="nav">
              {satuData && satuData.data.documents.map((n, i) => (
                <ListItem button key={i}>
                  <ListItemText primary={n.fields.name.stringValue} />
                </ListItem>
              ))}
            </List>
            <div className={classes.action}>
              <Button variant="raised" color="primary">
                Primary
              </Button>
            </div>
          </div>
        </Frame>
      </Fragment>
    );
  }
}

const mapStateToProps = createStructuredSelector({
  satuData: selectSatu(),
});

ContactMe.propTypes = {
  classes: PropTypes.object.isRequired,
  dispatch: PropTypes.func,
  satuData: PropTypes.object,
};

export default connect(mapStateToProps)(withRoot(withStyles(styles)(ContactMe)));



